import React, { Component } from 'react';
import { Row, Col, Input } from 'antd';
import '../static/css/app.css'
class login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: {
                bottom: 20,
                size: 16
            },
            password: {
                bottom: 20,
                size: 16
            }
        };
    }
    focusInput = (e) => {
        console.log(e)
        if (e === 0) {
            this.setState({
                name: {
                    bottom: 40,
                    size: 12
                }
            })
        } else {

            this.setState({
                password: {
                    bottom: 40,
                    size: 12
                }
            })
        }
    }
    blurInput = (e) => {
        console.log(e)
        if (e === 0) {
            this.setState({
                name: {
                    bottom: 40,
                    size: 12
                }
            })
        } else {

            this.setState({
                password: {
                    bottom: 40,
                    size: 12
                }
            })
        }
    }
    render() {
        return (
            <Row type="flex" justify="center" align="middle" className="login">
                <Col xs={20} sm={16} md={13} lg={10} xl={8}>

                    <div className="login-box">
                        <div className="login-input">
                            <div>
                                LOGIN
                            </div>
                            <div className="input-box">
                                <label className="name-label">账号</label>
                                <Input allowClear onFocus={this.focusInput.bind(this, 0)} onBlur={this.blurInput.bind(this, 0)}/>
                            </div>
                            <div className="input-box">
                                <label className="pwd-label" >密码</label>
                                <Input.Password onFocus={this.focusInput.bind(this, 1)} onBlur={this.blurInput.bind(this, 0)}/>
                            </div>
                            <a href="#">忘记密码？</a>
                        </div>
                        <div className="login-btn">
                            <button className="logon-btns">
                                注册
                            </button>
                            <button className="login-btns">
                                登录
                            </button>
                        </div>
                    </div>
                </Col>
                <style jsx>
                    {
                        `
                        .input-box .name-label{
                            position: absolute;
                            bottom: ${this.state.name.bottom}px;
                            left: 10px;
                            z-index: 10;
                            font-size: ${this.state.name.size}px;
                        }
                        .input-box .pwd-label{
                            position: absolute;
                            bottom: ${this.state.password.bottom}px;
                            left: 10px;
                            z-index: 10;
                            font-size: ${this.state.password.size}px;
                        }
                        `
                    }
                </style>
            </Row>
        );
    }
}

export default login;